import React, { useRef, useMemo, useState, useEffect } from 'react';
import {
  Cell,
  DatePicker,
  FormField,
  Layout,
  TimeInput,
} from '@wix/design-system';
import { BaseInputProps, BaseInputRef } from './types';
import {
  useWixPatternsContainer,
  useSelector,
  useEntityPageContext,
} from '@wix/patterns';
import { useController } from '@wix/patterns/form';

export interface DateTimeProps extends BaseInputProps<string | null> {}

export const DateTime = (props: DateTimeProps) => {
  const { field, dataHook, inputRef } = props;
  const { translate: t, ...container } = useWixPatternsContainer();
  useSelector(() => container.initTask.status);
  const pageState = useEntityPageContext();

  const controller = useController({
    name: field?.id ?? '',
    control: pageState.form.control,
    defaultValue: (pageState.entity as any)?.[field?.id ?? ''],
    rules: {
      validate: (v) => {
        if (
          field?.validation?.required &&
          (v === null || v === undefined || v === '')
        ) {
          return t('cairo.fieldValidation.requiredField');
        }
        return true;
      },
    },
  });

  const date = useMemo(() => {
    if (!controller.field.value) {
      return undefined;
    }
    const d = new Date(controller.field.value);
    return isNaN(d.getTime()) ? undefined : d;
  }, [controller.field.value]);

  const [dateStatusMessage, setDateStatusMessage] = useState('');
  const [timeStatusMessage, setTimeStatusMessage] = useState('');

  const ref = useRef<BaseInputRef>({});
  const dateRef = useRef<BaseInputRef>({});
  const timeRef = useRef<BaseInputRef>({});
  const [dateValue, setDateValue] = useState<number | string | undefined>(
    date?.getDate(),
  );
  const [timeValue, setTimeValue] = useState<number | string | undefined>(
    date?.getTime(),
  );

  useEffect(() => {
    ref.current.focus = () => {
      if (dateRef.current.invalid) {
        return dateRef.current.focus?.();
      }

      return timeRef.current.focus?.();
    };

    inputRef?.(ref.current);
  }, [inputRef]);

  return (
    <FormField
      label={field.displayName}
      required={field.validation?.required}
      dataHook={dataHook}
    >
      <Layout>
        <Cell span={6}>
          <DatePicker
            ref={(internalRef) => {
              dateRef.current.focus = () =>
                // TODO: PR to WSR needed to expose as public API
                // @ts-expect-error
                internalRef?.state?.inputRef?.focus?.();
            }}
            width="100%"
            value={date}
            // onChange is triggered only when you have valid, non-empty value. and onValidate is triggered on every change so we use onValidate
            onChange={() => {}}
            onValidate={({ validationType, value: newValue, format }) => {
              if (validationType === 'valid') {
                const dateWithNewDate = date
                  ? new Date(date)
                  : new Date(newValue);
                const _value = new Date(newValue);
                dateWithNewDate.setFullYear(_value.getFullYear());
                dateWithNewDate.setMonth(_value.getMonth());
                dateWithNewDate.setDate(_value.getDate());
                controller.field.onChange(dateWithNewDate.toISOString());
                setDateValue(new Date(newValue).getDate());
                setDateStatusMessage('');
                dateRef.current.invalid = false;
              } else if (validationType === 'formatError' && newValue === '') {
                if (field.validation?.required) {
                  setDateStatusMessage(
                    t('cairo.fieldValidation.requiredField'),
                  );
                  dateRef.current.invalid = true;
                } else {
                  setDateStatusMessage('');
                  dateRef.current.invalid = false;
                }
                setDateValue(newValue);
                if (!timeValue) {
                  controller.field.onChange(null);
                }
              } else {
                setDateStatusMessage(
                  t('cairo.customFields.fieldType.date.invalid.value', {
                    dateFormat: format ?? '',
                  }),
                );
                dateRef.current.invalid = true;
              }

              ref.current.invalid =
                dateRef.current.invalid || timeRef.current.invalid;
            }}
            status={
              controller.fieldState.error || dateStatusMessage
                ? 'error'
                : undefined
            }
            statusMessage={controller.fieldState.error || dateStatusMessage}
            dataHook={`datetime-date-${field.id}`}
          />
        </Cell>
        <Cell span={6}>
          <TimeInput
            ref={(internalRef) => {
              timeRef.current.focus = () => internalRef?.focus();
            }}
            dataHook={`datetime-time-${field.id}`}
            value={date ?? null}
            status={
              controller.fieldState.error || timeStatusMessage
                ? 'error'
                : undefined
            }
            statusMessage={controller.fieldState.error || timeStatusMessage}
            onInvalid={() => {
              timeRef.current.invalid = true;
              setTimeStatusMessage(
                t('cairo.customFields.fieldType.dateAndTime.invalid.value'),
              );
              ref.current.invalid =
                dateRef.current.invalid || timeRef.current.invalid;
            }}
            onChange={({ date: newValue }) => {
              timeRef.current.invalid = false;
              ref.current.invalid =
                dateRef.current.invalid || timeRef.current.invalid;

              if (newValue) {
                const dateWithNewTime = date ? new Date(date) : newValue;
                dateWithNewTime.setHours(newValue.getHours());
                dateWithNewTime.setMinutes(newValue.getMinutes());
                dateWithNewTime.setSeconds(newValue.getSeconds());
                dateWithNewTime.setMilliseconds(newValue.getMilliseconds());
                controller.field.onChange(dateWithNewTime.toISOString());
                setTimeStatusMessage('');
                setDateStatusMessage('');
              } else {
                if (!newValue) {
                  setTimeValue('');
                  if (field.validation?.required) {
                    setTimeStatusMessage(
                      t('cairo.fieldValidation.requiredField'),
                    );
                    timeRef.current.invalid = true;
                  } else {
                    setTimeStatusMessage('');
                    timeRef.current.invalid = false;
                  }
                  if (!ref.current.invalid) {
                    if (!dateValue) {
                      controller.field.onChange(null);
                    }
                  }
                }
              }
            }}
          />
        </Cell>
      </Layout>
    </FormField>
  );
};
